	import {baseIp} from "@/config.js"
    import {getUserInfo,setUserInfo,setCid,setHeadimg,getHeadimg} from '@/service.js';
    import {mapState,mapMutations} from 'vuex'
    import mInput from '../../components/m-input.vue'
    export default {
        components: {mInput},
        data() {
            return {
                providerList: [],
                hasProvider: false,
                account: '',
                password: '',
                positionTop: 0,
				
				userF:false,
				passF:false,
				
				version:"",	//版本号
				
				info:{
					latitude:'',
					longitude:'',
				}
            }
        },
        computed: mapState(['forcedLogin']),
		
		onLoad() {
			
			// uni.getLocation({
			// 	type: 'gcj02',
			// 	success: function (res) {
			// 		console.log('当前位置的经度：' + res.longitude);
			// 		console.log('当前位置的纬度：' + res.latitude);
			// 	}
			// });
			
			var that = this;



			

			// wx.getLocation({
			// 	 type: 'gcj02', //返回可以用于wx.openLocation的经纬度
			// 	 success (res) {
			// 	   const latitude = res.latitude
			// 	   const longitude = res.longitude
			// 	   wx.openLocation({
			// 		 latitude,
			// 		 longitude,
			// 		 scale: 18
			// 	   })
			// 	 }
			// 	})

			let userInfo = getUserInfo();
			var that = this;
			if(userInfo.isLogin === true){
				uni.reLaunch({
					url:"/pages/tabBar/mine/mine"
				})
			}
		},
        methods: {
			
			goReg(){
				uni.navigateTo({url:'/pages/register/register'})
			},
			goF(){
				uni.navigateTo({
					url:"/pages/pwd/pwd"
				})
			},
            ...mapMutations(['login']),



















			
            bindLogin() {
                /**
                 * 客户端对账号信息进行一些必要的校验。
                 * 实际开发中，根据业务需要进行处理，这里仅做示例。
                 */
                if (this.account.length < 5) {
                    uni.showToast({icon: 'none',title: '账号最短为 5 个字符'});
                    return;
                }
                if (this.password.length < 6) {
                    uni.showToast({icon: 'none',title: '密码最短为 6 个字符'});
                    return;
                }
                const data = {
                    account: this.account,
                    password: this.password
                };
				uni.showLoading({title:"登录中...",mask:true});
				var that = this,
				
					url = `${baseIp()}/ams/system/distribute.htm?module=login&methodyd=1&username=${data.account}&password=${data.password}`;
				// console.log(url)
				// return;
				uni.request({
					url,
					success: (res) => {
						var data = res.data;
						// console.log(data)
						uni.setStorageSync("type",res.data.msg)
						uni.hideLoading();
						if(data.code == 1){
							if(data.user.headimg){
								var headimg = data.user.headimg;
								var i = headimg.indexOf("ams");
								var img = headimg.substring(i);
								img = img.replace(/\\/g, '/')
								headimg = `${baseIp()}/${img}`;
								data.user.headimg = headimg;
								setHeadimg(headimg);
							}
							setUserInfo(data.user);
							that.login();



							uni.switchTab({
								url:"/pages/tabBar/information/information"
							})
						}else{
							uni.showToast({icon: 'none',title: data.message,});
							this.account = "";
							this.password = "";
						}
					},
					fail:(err)=>{
						console.log(err)
						uni.hideLoading();
						uni.showToast({title:err.errMsg,mask:true,icon:"none"})
					}
				});
            },
        },
    }
